.compass-container {
    position: relative;
    width: 800px;
    height: 800px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.compass-image {
    width: 100%;
    height: 100%;
    transform-origin: center;
}
